@import url('https://fonts.googleapis.com/css2?family=Goblin+One&family=Martian+Mono:wght@200;300;400;500;600;700&display=swap');

.navbar {
  display: flex;
  padding-top: 70px;
  padding-left: 200px;
  padding-right: 200px;
  gap: 400px;
  justify-content: space-around;
  color: #49A078;
  font-family: 'Martian Mono', monospace;
  font-size: 10px;
  font-weight: 200;
  align-items: center;
  background-color: #1F2421;
}

.landing {
  width: 100%;
  height: 100vh;
  background-color: #1F2421;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: center;
}

.landing h1 {
  font-family: 'Goblin One', cursive;
  color: #49A078;
  font-size: 100px;
  width: 1300px;
  line-height: 1.1em;
  padding-top: 50px;
}

.container {
  width: 100%;
  height: 150vh;
  background-color: #49A078;
}

.box {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding-top: 150px;
}

.text-box {
  width: 550px;
}

.text-box h1 {
  font-size: 32px;
  line-height: 1.2em;
  font-family: 'Goblin One', cursive;
}

.text-box p {
  font-size: 16px;
  line-height: 1.5em;
  font-family: 'Martian Mono', monospace;
}

.start-container {
  display: grid;
  justify-content: center;
  text-align: center;
  background-color: #49A078;
  height: 50vh;
  align-content: center;
  gap: 20px;
}

.start-container h1 {
  font-family: 'Goblin One', cursive;
  color: #1F2421;
  font-size: 48px;
  line-height: 1.2em;
  margin: 0;
}

.start-container p {
  font-family: 'Martian Mono', monospace;
  color: #1F2421;
  font-size: 16px;
  line-height: 1.5em;
  width: 400px;
  margin: 0;
}

.btns {
  display: flex;
  gap: 10px;
}

button {
  width: 200px;
  height: 40px;
  border-radius: 8px;
  border: none;
  font-family: 'Martian Mono', monospace;
}

button:nth-child(1) {
  color: #fff;
  background-color: #216869;
}

button:nth-child(2) {
  color: #49A078;
  background-color: #1F2421;
}

.gallery-container {
  width: 100%;
  height: 150vh;
  background: #49A078;
  display: grid;
  grid-template-columns: repeat(2, 30%); /* Two items per row */
  justify-content: center;
  place-items: center;
  padding-top: 50px;
}

.gallery-box {
  width: 500px;
}

.gallery-box h1 {
  font-family: 'Goblin One', cursive;
  font-size: 32px;
  color: #1F2421;
  width: 450px;
  margin: 0;
}

.gallery-box p {
  font-family: 'Martian Mono', monospace;
  font-size: 20px;
  color: #fff;
  width: 400px;
}

.footer-container {
  height: 20vh;
  background-color: #49A078;
  color: #fff;
  fill: #fff;
  text-align: center;
  padding-top: 400px;
}

.icons {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.footer-container img{
  width: 3%;
}

.footer-container p {
  font-size: 16px;
  color: #fff;
  line-height: 1.5em;
  font-family: 'Martian Mono', monospace;
  margin: 0;
}
